TransferDom
},
components: {
PopupPicker,
Group,
XAddress,
ChinaAddressV4Data,
XButton,
Box,
XTextarea,
Alert,
},
data() {
return {
title1: "",
list1: [["", ""]],
value1: [],
placeholder1: "",

title2: "",
list2: [["", ""]],
value2: [],
placeholder2: "",

title3: "",
value3: [],
