title4: "",
placeholder4:
"",

title5: "",
placeholder5: "",

show: false,
title6: "",
summary: ""
};
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data);
},
onShow() {
console.log("on show");
},
onHide(type) {
console.log("on hide", type);
},
onChange(val) {
console.log("val change", val);
},
submit() {
this.show = true;
}
}
};



