
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
PopupPicker,
Group,
XAddress,
ChinaAddressV4Data,
XButton,
Box,
XTextarea,
Alert,
},
data() {
return {
title1: "",
list1: [["", ""]],
value1: [],
placeholder1: "",

title2: "",
list2: [["", ""]],
value2: [],
placeholder2: "",






title3: "",  // 
value3: [],
addressData: ChinaAddressV4Data,


title4: "",
placeholder4:
"",












title5: "",
placeholder5: "",

show: false,
title6: "",
summary: ""
};
},
created() {
if (this.$route.params.type == "wish") {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/wish_bar.jpeg';
} else if (this.$route.params.type == "goodnight") {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/goodnight_bar.jpeg';
this.title4 = "";
this.placeholder4 = "";
} else {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/trouble_bar.jpg';
this.title2 = "";
this.title4 = "";
this.placeholder4 = "";

}
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data);
},
onShow() {
console.log("on show");
},
onHide(type) {
console.log("on hide", type);
},
onChange(val) {
console.log("val change", val);
},
submit() {
this.show = true;
}
}
};



